body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.svg-root {
  background-color: #ccc;
}

.App {
  margin: 10px;
}

.App h1 {
  border-bottom: solid 3px black;
  margin-bottom: 10px;
}

.App p {
  margin: 5px;
}

.svgZoom {
  display: block;
  position: relative;
}

.axis path {
  display: none;
}

.axis line {
  stroke-opacity: 0.3;
  shape-rendering: crispEdges;
}

.view {
  fill: url(#gradient);
  stroke: #000
}

.svgZoom button {
  position: absolute;
  top: 20px;
  left: 20px;
}


.node circle {
  fill: blue;
  border: black;
  stroke: black; 
  stroke-width: 0.5px;
  opacity: 0.6;
}

.link {
  fill: none;
  stroke: #555;
  stroke-opacity: 0.4;
  stroke-width: 1.5px;
}

